<template>
  <v-container v-on:keyup.enter="login">
    <v-row class="text-center">
      <v-col cols="12">
        <v-img
          :src="require('../assets/vertologo.jpg')"
          class="my-3"
          contain
          height="200"
        />
        <div>
          <span class="text-h6">Verto Health Technical Exercise - Trevor Harvey</span>
        </div>
      </v-col>
    </v-row>
    <v-row class="d-flex justify-center">
      <v-col  cols=4 xl=4 xxl=4 lg=4 md=6 sm=10 xs=10>
        <v-form >

          <v-text-field v-model="form.username" label="Username"></v-text-field>
          <v-text-field v-model="form.password" label="Password"></v-text-field>
          <div class="d-flex justify-center mt-3">
            <v-btn @click="login" elevation="2">Login!</v-btn></div>
        </v-form>
      </v-col>
    </v-row>

      <v-snackbar
        centered
      v-model="tryagain"
      :timeout=2000
    > Invalid Credentials
      <template v-slot:action="{ attrs }">
        <v-btn
          color="blue"
          text
          v-bind="attrs"
          @click="tryagain = false"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>
    
  </v-container>
</template>

<script>
import { mapActions } from 'vuex'
import { mapGetters } from 'vuex'

  export default {
    name: 'Loginform',
    
    methods: {
       ...mapActions({
        authenticate: 'authenticate',
      }),

      login: async function(){
        await this.authenticate(this.form)
          if(this.authenticated == true){
            console.log(this.user+" Has Been Authenticated!")
            console.log(this.token)
          this.$router.replace({
            name: 'home'
          })
          
          } else {
            this.tryagain = true
          }      
      },
    },

    data: () => {
      return {
        form:{
          "username":"",
          "password":""
          },
        tryagain:false,
        }
    },

    computed: {
      ...mapGetters ({
        authenticated: 'authenticated',
        user: 'username',
        token: 'token'
      })
    }
  }
</script>
