<template>
  <v-app>
    <v-main>
        <v-container>
            <div class="d-flex justify-end mt-4 mr-4">
                <v-btn elevation="2" @click='submitlogout'>Logout</v-btn>
            </div>

            <div class="d-flex justify-center mt-4 mr-4">
                <span class="text-h6">Hello {{ username }}</span>
            </div>

            <v-row class="d-flex justify-center mt-15">
                <v-col cols=5>
                    <v-text-field v-model="search" label="Search by name or id"></v-text-field> 
                    <v-select
                        label="Filter by department" 
                        :items="departments" 
                        v-model="filter" 
                        v-on:input="searchemployees">
                    </v-select>
                </v-col>
                <v-btn 
                    @click="searchemployees" 
                    elevation="2">
                    Search
                </v-btn>
            </v-row>

            <v-row class="d-flex justify-center">
                <v-col cols=9>
                    <v-data-table
                    item-key= "id"
                    :headers="headers"
                    :items="table"
                    :items-per-page="10"
                    class="elevation-1"
                    >

                    <template v-slot:top>
                        <v-btn @click="openaddemployee" icon x-large>
                            <v-icon>mdi-account-plus</v-icon>
                        </v-btn>
                    </template>

                    <template v-slot:item.buttons="{ item }">
                    <v-btn icon>
                        <v-icon @click="opendeleteemployee(item)">mdi-delete</v-icon>
                    </v-btn>
                    <v-btn icon>
                        <v-icon  @click="openupdateemployee(item)">mdi-account-edit-outline</v-icon>
                    </v-btn>
                    </template>

                    </v-data-table>
                </v-col>
            </v-row>

            <!---------------------- dialogs/modals -------------------------------->

    <v-dialog
        v-model="newEmployeeform"
        width="500">
       <v-card>
            <v-card-title class="headline grey lighten-2">
                <h1 class="text-h4 mx-auto"> New Employee </h1>
            </v-card-title>
 
            <v-card-text class="mt-5">
                <p class="text-h5 text-center">Enter new employee details: </p>
            </v-card-text>

            <v-form ref="newform" v-if="selected != []">
                <v-card-text>
                    <v-text-field
                    v-model="employeeinfo.first" 
                    :rules="formRules"
                    color="grey" 
                    class="mt-n5" 
                    label="First Name">
                    </v-text-field>

                    <v-text-field 
                    :rules="formRules"
                    v-model="employeeinfo.last"  
                    color="grey"
                    label="Last Name">
                    </v-text-field>
                    
                    <v-select
                    :rules="selectRules"
                    label="Department" 
                    :items="departments.slice(1)" 
                    v-model="employeeinfo.dept">
                    </v-select>
                </v-card-text>

                <v-card-actions>  
                    <v-row class="d-flex justify-center mb-3">
                        <v-btn 
                            class="mr-10 py-5 my-2"
                            color="green lighten-4"
                            rounded
                            @click='addemployee'
                          >
                        Add Employee
                        </v-btn>
                        <v-btn 
                            class="py-5 my-2"
                            color="deep-orange accent-1"
                            rounded
                            @click="newEmployeeform = false">
                        Cancel
                        </v-btn>
                    </v-row>
                </v-card-actions>
            </v-form>
        </v-card>
    </v-dialog>


      <v-dialog
        v-model="updateEmployeeform"
        width="500">
       <v-card>
            <v-card-title class="headline grey lighten-2">
                <h1 class="text-h4 mx-auto"> Update Employee </h1>
            </v-card-title>
 
            <v-card-text class="mt-5">
                <p class="text-h5 text-center">Update employee details: </p>
            </v-card-text>

            <v-form ref="newform" v-if="selected != []">
                <v-card-text>
                    <v-text-field
                    v-model="employeeinfo.firstname" 
                    :rules="formRules"
                    color="grey" 
                    class="mt-n5" 
                    label="First Name">
                    </v-text-field>

                    <v-text-field 
                    :rules="formRules"
                    v-model="employeeinfo.lastname"  
                    color="grey"
                    label="Last Name">
                    </v-text-field>
                    
                    <v-select
                    :rules="selectRules"
                    label="Department" 
                    :items="departments.slice(1)" 
                    v-model="employeeinfo.dept">
                    </v-select>
                </v-card-text>

                <v-card-actions>  
                    <v-row class="d-flex justify-center mb-3">
                        <v-btn 
                            class="mr-10 py-5 my-2"
                            color="green lighten-4"
                            rounded
                            @click='updateemployee'
                          >
                        Update Employee
                        </v-btn>
                        <v-btn 
                            class="py-5 my-2"
                            color="deep-orange accent-1"
                            rounded
                            @click="updateEmployeeform = false;">
                        Cancel
                        </v-btn>
                    </v-row>
                </v-card-actions>
            </v-form>
        </v-card>
    </v-dialog>


       <v-dialog
        v-model="deleteEmployeeform"
        width="500">
       <v-card>
            <v-card-title class="headline grey lighten-2">
                <h1 class="text-h4 mx-auto"> Delete Employee </h1>
            </v-card-title>
 
            <v-card-text class="mt-5">
                <p class="text-h5 text-center">Are you sure you wish to delete </p>
                 <p class="text-h5 text-center"> {{ employeeinfo.firstname }} {{employeeinfo.lastname}}?</p>
            </v-card-text>

            <v-form ref="newform" v-if="selected != []">
                <v-card-actions>  
                    <v-row class="d-flex justify-center mb-3">
                        <v-btn 
                            class="mr-10 py-5 my-2"
                            color="green lighten-4"
                            rounded
                            @click='deleteemployee'
                          >
                        Delete Employee
                        </v-btn>
                        <v-btn 
                            class="py-5 my-2"
                            color="deep-orange accent-1"
                            rounded
                            @click="deleteEmployeeform = false;">
                        Cancel
                        </v-btn>
                    </v-row>
                </v-card-actions>
            </v-form>
        </v-card>
    </v-dialog>


     <v-snackbar
        centered
      v-model="Snackbar"
      :timeout=2000
    > {{ snackbartext }}
      <template v-slot:action="{ attrs }">
        <v-btn
          color="blue"
          text
          v-bind="attrs"
          @click="Snackbar = false"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>


        </v-container>
    </v-main>
  </v-app>
</template>

<script>
import axios from "axios"
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'


export default {
  name: 'Home',
  components: {
  },
  mounted: async function (){
        try{ 
            console.log('using token'+this.token)
            let table = await axios.get('./api/employees',{headers:{ "Authorization": "Bearer "+this.token}})
            this.table = table.data
            console.log(this.table)}
        catch(e){
            this.logout,
            this.$router.push('/')
            }
  },

  data: () => {
    return {
        table:[],
        search:'',
        Snackbar:false,
        newEmployeeform:false,
        snackbartext:'',
        employeeinfo:{
            first:"",
            last:"",
            dept:"",
            id:""
        },
        deleteEmployeeform:false,
        updateEmployeeform:false,
        filter:'',
        selected: [],
        departments:["All Departments","Engineering","Marketing","Product Management","Project Management","Quality Control","Sales","Customer Service","Human Resources","Finance"],
        headers: [{
              text:"ID",
              align:"start",
              value:"id"},
              {text:"First Name",value:"firstname"},
              {text:"Last Name",value:"lastname"},
              {text:"Dept",value:"dept"},
              {text:"Actions",value:"buttons",sortable: false,}
             ],
        formRules: [
            v => !!v || 'Field is required',
            v => v.length > 1 || 'Must be at least 2 characters',       
            ],
        selectRules: [
            v => !!v || 'Field is required',      
            ],
    }
  },
  methods:{
    
    checkping: async function(){
        return new Promise((resolve,reject)=>{
             axios.get('/api/ping',{headers:{ "Authorization": "Bearer "+this.token}}).then(() => {
        resolve(true);
        }).catch(() => {
         
         reject(this.$router.push('/'))
    })
        })
       
    },

    reload: async function(){
          let table = await axios.get('./api/employees',{headers:{ "Authorization": "Bearer "+this.token}})
          this.table = table.data
    },

    openaddemployee: async function(){
        try{await this.checkping()
        this.newEmployeeform = true}
        catch(e){this.$router.push('/')}
    },

    openupdateemployee: async function(employee){
        try{await this.checkping()
        this.employeeinfo = Object.assign({},employee)
        this.updateEmployeeform = true}
        catch(e){this.$router.push('/')}
    },

    opendeleteemployee: async function(employee){
         try{await this.checkping()
         this.employeeinfo = Object.assign({},employee)
        this.deleteEmployeeform = true}
        catch(e){this.$router.push('/')}
    },

    searchemployees: async function(){
        try{await this.checkping()
        var filterstr = ""
        if (this.filter != "All Departments"){filterstr = this.filter}
        let searchResult = await axios.get('/api/employees?search='+this.search+'&filter='+filterstr,{headers:{"Authorization": "Bearer "+this.token}})
        this.table = searchResult.data}
        catch(e){this.$router.push('/')}
        
    },

    submitlogout: async function(){
        this.logout()
        this.$router.push('/')

    },

    addemployee: async function() {
        if(this.$refs.newform.validate()){
            try{await this.checkping()
                await axios.post('/api/employees',this.employeeinfo,{headers:{"Authorization": "Bearer "+this.token}})
                this.newEmployeeform = false
                this.snackbartext = "Success"
                this.Snackbar = true
                this.reload()

                 }
            catch(e){
                this.newEmployeeform = false
                this.snackbartext = "Error"
                this.Snackbar = true
                } 
        }
    },
    updateemployee: async function() {
        if(this.$refs.newform.validate()){
            try{await this.checkping()
                await axios.patch('/api/employees',this.employeeinfo,{headers:{"Authorization": "Bearer "+this.token}})
                this.updateEmployeeform = false
                this.snackbartext = "Success"
                this.Snackbar = true
                this.reload()

                 }
            catch(e){
                this.newEmployeeform = false
                this.snackbartext = "Error"
                this.Snackbar = true
                } 
        }
    },

    deleteemployee: async function() {
            try{await this.checkping()
                await axios.delete('/api/employees',{data:this.employeeinfo,headers:{"Authorization": "Bearer "+this.token}})
                this.deleteEmployeeform = false
                this.snackbartext = "Success"
                this.Snackbar = true
                this.reload()

                 }
            catch(e){
                this.deleteEmployeeform = false
                this.snackbartext = "Error"
                this.Snackbar = true
                } 
        
    },


     ...mapActions({
        logout: 'logout',
      }),
 
 },


  computed:{
      ...mapGetters({
          token:'token',
          username:'username'
      })
  }
};
</script>
