import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import createPersistedState from "vuex-persistedstate"

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
      authenticated:false,
      username:null,
      session:null,
  },
  getters: {
      authenticated (state) {
        return state.authenticated == true
      },
      username (state){
        return state.username
      },
      token (state){
        return state.session
      }
  },
  mutations: {
    authenticate (state,data) {
        state.authenticated=true
        state.session=data.SessionID
        state.username=data.User
    },
    logout (state) {
        state.authenticated=false
        state.session=null
        state.username=null
    }
  },
  actions: {
    async authenticate ({ dispatch },credentials ) {
        try{
        let response = await axios.post('/api/login', credentials)
        console.log(response.data)
        return dispatch('login', response.data)}
        catch (e) {
          return false
        }    
            },

    login ({ commit }, data) {
        commit('authenticate',data)
        return true
    },

    logout ({ commit }) {
        commit('logout')       
    },

  },
  modules: {

  },
  plugins: [createPersistedState()]
})
